.comment_input {
    display: flex;
    align-items: center;
}

.comment_input input {
    background-color: rgba(0, 0, 0, -0.97) !important;
    border: none;
    outline: none;
    flex: 1;
    overflow: auto;
}

.comment_input .dropdown-menu {
    transform: translate3d(-120px, -200px, 0px) !important;
}

.comment_input .file_upload {
    position: relative;
    overflow: hidden;
    margin: 0 10px;
}

.comment_input .file_upload #file {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
}


/*-------------Comments------------*/

.comment_display {
    padding: 10px 25px;
}

.comment_card .comment_content {
    background: #eee;
    padding: 7px;
    border-radius: 10px;
    border-top-left-radius: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.comment_card .comment_content .nav-item {
    cursor: pointer;
    opacity: 0;
}

.comment_card .comment_content:hover .nav-item {
    opacity: 1;
}

.comment_card .comment_content .dropdown-menu {
    position: absolute;
    right: 0;
    left: inherit;
}

.comment_card .comment_content .dropdown-item {
    display: flex;
    margin: 10px 0;
}

.comment_card .comment_content textarea {
    width: 100%;
    border: none;
    outline: none;
}

.card-footer {
    /* padding: 0.75rem 1.25rem;
